.resistor-container {
    height: 200rem;
}

.resistor-container-5-14 {
    height: 135rem;
}

.resistor-container-5-12 {
    height: 130rem;
}

.resistor-container-5-1 {
    height: 50rem;
}

.resistor-container-5-2 {
    height: 15rem;
}

.resistor-container-5-5 {
    height: 15rem;
}

@media (min-width: 768px) {

    .resistor-table {
        width: 50%;
        margin-left: auto;
        margin-right: auto;
    }

    .minimize {
        width: 50%;
        margin-left: auto;
        margin-right: auto;
    }

    .resistor-container {
        height: 70rem;
    }

    .resistor-container-5-14 {
        height: 80rem;
    }

    .resistor-container-5-12 {
        height: 50rem;
    }

    .resistor-container-5-1 {
        height: 35rem;
    }

    .resistor-container-5-2 {
        height: 20rem;
    }
    
    .resistor-container-5-5 {
        height: 15rem;
    }

}

/* 
5% 1/4W height: 80rem @media 135rem
1% 1/4W height: 70rem @media 200rem
5% 1/2W height: 50rem @media 130rem
*/